"use client";

import { useState, useCallback, useRef, useEffect } from 'react';
import { IMessage } from '@/types/chat';
import { sendMessageToAI, generateMessageId } from '@/lib/ai-service';

export function useChat() {
  const [messages, setMessages] = useState<IMessage[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages are added
  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, []);

  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  const sendMessage = useCallback(async (content: string) => {
    if (!content.trim() || loading) return;

    setError(null);
    
    // Create user message
    const userMessage: IMessage = {
      id: generateMessageId(),
      role: 'user',
      content: content.trim(),
      timestamp: Date.now(),
    };

    // Optimistically add user message
    setMessages(prev => [...prev, userMessage]);
    setLoading(true);

    try {
      // Get AI response
      const aiResponse = await sendMessageToAI(content.trim());
      
      // Create AI message
      const aiMessage: IMessage = {
        id: generateMessageId(),
        role: 'assistant',
        content: aiResponse,
        timestamp: Date.now(),
      };

      // Add AI response to messages
      setMessages(prev => [...prev, aiMessage]);
    } catch (err: any) {
      setError(err.message || 'Failed to get AI response');
      console.error('Chat error:', err);
    } finally {
      setLoading(false);
    }
  }, [loading]);

  const clearChat = useCallback(() => {
    setMessages([]);
    setError(null);
  }, []);

  const retryLastMessage = useCallback(() => {
    if (messages.length === 0 || loading) return;
    
    const lastUserMessage = [...messages].reverse().find(msg => msg.role === 'user');
    if (lastUserMessage) {
      // Remove messages after the last user message
      const lastUserIndex = messages.findIndex(msg => msg.id === lastUserMessage.id);
      setMessages(prev => prev.slice(0, lastUserIndex + 1));
      sendMessage(lastUserMessage.content);
    }
  }, [messages, loading, sendMessage]);

  return {
    messages,
    loading,
    error,
    sendMessage,
    clearChat,
    retryLastMessage,
    messagesEndRef,
  };
}
