"use client";

import { useChat } from '@/hooks/useChat';
import ChatMessage from './ChatMessage';
import ChatInput from './ChatInput';
import { Button } from '@/components/ui/button';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Alert, AlertDescription } from '@/components/ui/alert';

export default function ChatContainer() {
  const { 
    messages, 
    loading, 
    error, 
    sendMessage, 
    clearChat, 
    retryLastMessage,
    messagesEndRef 
  } = useChat();

  return (
    <div className="flex flex-col h-[calc(100vh-200px)] max-w-4xl mx-auto">
      {/* Chat Header */}
      <div className="flex justify-between items-center mb-4 pb-4 border-b border-border">
        <div>
          <h2 className="text-lg font-semibold">Chat with AI</h2>
          <p className="text-sm text-muted-foreground">
            {messages.length === 0 
              ? 'Start a conversation with the AI assistant' 
              : `${messages.length} message${messages.length !== 1 ? 's' : ''}`
            }
          </p>
        </div>
        {messages.length > 0 && (
          <div className="flex gap-2">
            {error && (
              <Button
                variant="outline"
                size="sm"
                onClick={retryLastMessage}
                disabled={loading}
              >
                Retry
              </Button>
            )}
            <Button
              variant="outline"
              size="sm"
              onClick={clearChat}
              disabled={loading}
            >
              Clear Chat
            </Button>
          </div>
        )}
      </div>

      {/* Messages Area */}
      <div className="flex-1 border border-border rounded-lg bg-card">
        <ScrollArea className="h-full p-4">
          {messages.length === 0 ? (
            <div className="flex items-center justify-center h-full text-center">
              <div className="max-w-md">
                <h3 className="text-lg font-medium mb-2">Welcome to AI Chat</h3>
                <p className="text-muted-foreground mb-4">
                  Start a conversation by typing a message below. The AI assistant is ready to help you with questions, creative tasks, and more.
                </p>
                <div className="text-sm text-muted-foreground space-y-1">
                  <p>• Press Enter to send a message</p>
                  <p>• Use Shift+Enter for line breaks</p>
                  <p>• Click "Clear Chat" to start over</p>
                </div>
              </div>
            </div>
          ) : (
            <div className="space-y-1">
              {messages.map((message) => (
                <ChatMessage key={message.id} message={message} />
              ))}
              
              {/* Loading indicator */}
              {loading && (
                <div className="flex justify-start mb-4">
                  <div className="bg-muted text-muted-foreground rounded-lg px-4 py-3 max-w-[80%]">
                    <div className="flex items-center space-x-2">
                      <div className="flex space-x-1">
                        <div className="w-2 h-2 bg-current rounded-full animate-bounce"></div>
                        <div className="w-2 h-2 bg-current rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                        <div className="w-2 h-2 bg-current rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                      </div>
                      <span className="text-sm">AI is thinking...</span>
                    </div>
                  </div>
                </div>
              )}
              
              {/* Scroll anchor */}
              <div ref={messagesEndRef} />
            </div>
          )}
        </ScrollArea>
      </div>

      {/* Error Display */}
      {error && (
        <Alert className="mt-4 border-destructive/50 text-destructive">
          <AlertDescription className="flex items-center justify-between">
            <span>{error}</span>
            <Button
              variant="outline"
              size="sm"
              onClick={() => window.location.reload()}
              className="ml-2"
            >
              Refresh
            </Button>
          </AlertDescription>
        </Alert>
      )}

      {/* Input Area */}
      <div className="mt-4 pt-4 border-t border-border">
        <ChatInput onSendMessage={sendMessage} loading={loading} />
      </div>
    </div>
  );
}
